import React from "react";

class FormBody extends React.Component {
    render () {
        return(
            <div className="ui main">
                
                <form className="ui form">
                <fieldset>
                    <legend>Personal Information</legend>
                    <div className="field">
                        <label>Full Name</label>
                        <input type="text" name="fullname" placeholder="Full name" />
                    </div>
                    <div className="field">
                        <label>Email Address</label>
                        <input type="email" name="email" placeholder="you@example.com" />
                    </div>
                    <div className="field">
                        <label>Phone Number</label>
                        <input type="text" name="phoneno" placeholder="0803 000 0000" />
                    </div>
                    </fieldset>

                    <fieldset>
                    <legend>Educational Experience</legend>
                    <div className="field">
                        <label>Name of School</label>
                        <input type="text" name="schoolname" placeholder="Name of School" />
                    </div>
                    <div className="field">
                        <label>Title of Study</label>
                        <input type="text" name="titleofstudy" placeholder="Mechanical Engineering" />
                    </div>
                    <div className="field">
                        <label>Year of Graduation</label>
                        <input type="text" name="yearofgraduation" placeholder="2015" />
                    </div>
                    </fieldset>

                    <fieldset>
                    <legend>Practical Experience</legend>
                    <div className="field">
                        <label>Company Name</label>
                        <input type="text" name="companyname" placeholder="Company Name" />
                    </div>
                    <div className="field">
                        <label>Position Title</label>
                        <input type="text" name="positiontitle" placeholder="Position Title" />
                    </div>
                    <div className="field">
                        <label>Main Task</label>
                        <textarea name="maintask" rows="3" column="auto"></textarea>
                    </div>

                    <div className="field">
                        <label>Date From</label>
                        <input type="date" name="datefrom" />
                    </div>
                    <div className="field">
                        <label>Date To</label>
                        <input type="date" name="dateto" />
                    </div>
                    </fieldset>

                    <br></br>
                    <div>
                        <button class="ui primary button" type="submit">Submit</button>
                    </div>
                    
                </form>
            </div>
        );
    }
}

export default FormBody;